export default {
	data() {
		return {
			stories:[],
			myid:-1,
			autoplay:true
		}
	},
	methods: {
		getstory() {
			uni.request({
				url:'https://web58.top/zt/json/nhqc/stories.json',
				success: (res) => {
					
					this.stories=res.data.stories
					//console.log(this.stories)
				}
			})
		},
		myshow(id) {
			this.myid = id
		},
		close() {
			this.myid = -1
		}
	},
	onLoad() {
		this.getstory();
	}
}
